import React from "react";
import { ZpageWraperHOC } from "zerod";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZpageWrapper/doc.md";
import pageHeader from '@/views/Main/component-doc/ZpageHeader-doc/pageHeader';
export default AmdDocHOC(doc, {
	demo1: function() {
		const PageWraper = ZpageWraperHOC();
		const newpageHeader = {
            show:true,
			...pageHeader
		};
		const MyScript = AshowDemoHOC(
			<PageWraper pageHeader={newpageHeader}>
				<div className="z-panel">
					<div className="z-panel-body">中间内容</div>
				</div>
			</PageWraper>,
			true,
		);
		return <MyScript />;
	},
});
